body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.NavBar {
    display: flex;
    align-items: center;
    position: fixed;
    top: 0; 
    left: 0; 
    z-index: 9999; 
    background-color: #b6c199;
    height: 60px;
    box-shadow: 2px 2px 2px;
  }

.NavBar-Nav {
    float: right;
    display: flex;
    flex-direction: row;
    margin: 0;
}

.nav-item {
    width: 100px;
    text-align: center;
    list-style-type: none;
}

.container-fluid {
    width: 100vw;
}

nav a {
    text-decoration: none;
}

nav a.active {
    font-weight: bold;
}

nav a.active:hover {
    color: #687d9c !important;
}

nav a:hover {
    text-decoration: none;
    color: #687d9c;
}

.navbar-brand {
    margin: 20px;
    font-size: 1.5em;
    font-weight: 600;
}

nav a:not(.navbar-brand):hover {
    font-weight: bold;
}

.navbar-brand, .navbar-link {
    color: rgb(105, 99, 99);
}
.CompanyCard {
    display: flex;
    justify-content: center;
}

.CompanyCard-card {
    border: solid grey 1px;
    margin: 20px;
    width: 50vw;
    border-radius: 10px;
    background-color: #c7ddcc;
    border: 1.5px solid;
    box-shadow: 5px 2px 2px;
}

.CompanyCard-Link {
    text-decoration: none;
    color: black;
}

.CompanyCard-Link:hover {
    text-decoration: none;
    color: #2F5C8F;
}

.CompanyCard-title {
    font-size: 1.5em;
    font-weight: 600;
}
.pagination {
    display: flex;
    justify-content: center;
    list-style-type: none;
    padding-left: 0;
}

.page-item {
    width: 30px;
    height: 30px;
    border-color: black;
    border-style: solid;
    border-radius: 5px;
    border-width: 1px;
    margin-left: 5px;
}

.page-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px; 
}

.page-item:hover {
    border-style: none;
    background-color: #778beb;
}

/*this does not seem to work. Why is the link not active?*/
a:target {
    font-weight: 700;
}

.pagination-nav {
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
}
